import React, { useState, useEffect } from "react";
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import mTrans from '../../../../Controler/c_Transaksi'
import Notifikasi from '../../../../helpers/Notifikasi'
import { isArray } from "util";


const Modal = (props) => {
    const authLogin = props.authLogin
    const janji = props.janji
    const [isAlert, setIsAlert] = useState(false)
    const [alertText, setAlertText] = useState("")
    const [alasanBatal, setAlasanBatal] = useState("")

    const handleClose = () => {
        props.setOpen(false);
    };
    const simpan = () => {
        if (alasanBatal.length <= 4) {
            setAlertText("Wajib isi alasan batal minimal 5 huruf!!!")
            setIsAlert(true)
        } else {
            mTrans.batalkanJanji(authLogin.clientID, authLogin.token, janji._id, alasanBatal).then(resp => {
                if (resp) {
                    Notifikasi("success", "Berhasil batalkan janji", "")
                    props.setIsReload(true)
                    handleClose()
                } else {
                    Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
                }
            }).catch(err => {
                if (isArray(err) && err.length > 0) {
                    Notifikasi("error", err[0].message, "Gagal Simpan")
                } else if (typeof err) {
                    Notifikasi("error", err.message, "Gagal Simpan")
                } else {
                    console.log(err);
                }
            })
        }
    }
    useEffect(() => {
        const timer = setTimeout(() => {
            setIsAlert(false)
        }, 3000);
        return () => clearTimeout(timer);
    }, [isAlert]);

    return (
        <Dialog open={props.open} onClose={handleClose} aria-labelledby="form-dialog-title">
            <DialogTitle id="form-dialog-title">Form Batal Janji</DialogTitle>
            {isAlert && (
                <DialogContentText>
                    <Alert severity="error" onClose={() => { setIsAlert(false) }}>{alertText}</Alert>
                </DialogContentText>
            )}
            <DialogContent>
                <TextField
                    autoFocus
                    margin="dense"
                    id="alasanBatal"
                    label="Alasan Batal"
                    fullWidth
                    multiline={false}
                    required
                    value={alasanBatal}
                    onChange={(e) => setAlasanBatal(e.target.value)}
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={handleClose} color="secondary">
                    Cancel
          </Button>
                <Button onClick={simpan} color="primary">
                    Oke
          </Button>
            </DialogActions>
        </Dialog>
    )
}
export default Modal;
