    listPembayaran: (clientID, token, kodeklinik, tanggal) => {
        return new Promise((resolve, reject) => {
            const query = `
                query {
                    listPembayaran(kodeklinik:"${kodeklinik}",tanggal:"${moment(tanggal).format("YYYY-MM-DD")}"){
                        _id
                        kodetrans
                        kodedokter
                        pasien
                        janji{
                          tanggalJanji
                          jamMulaiJanji
                          jamSelesaiJanji
                        }
                        keperluanPasien
                        layanan{
                           kodeLayanan
                          namaLayanan
                          harga
                          qty
                        }
                        peralatan{
                          kodeProduk
                          namaProduk
                          harga
                          qty
                          satuan
                        }
                        resepObatdokter{
                          kodeProduk
                          namaProduk
                          harga
                          qty
                          satuan
                        }
                  }
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));
                const listBayar = []
                resp.data.listPembayaran.map(el => {
                    return listBayar.push({
                        ...el,
                        tanggalJanji: el.janji.tanggalJanji,
                        totObat: el.resepObatdokter.length,
                        totBayar: el.layanan.map(el => el.qty * el.harga).reduce((a, b) => a + b, 0) + el.resepObatdokter.map(el => el.qty * el.harga).reduce((a, b) => a + b, 0) + el.peralatan.map(el => el.qty * el.harga).reduce((a, b) => a + b, 0)
                    })
                })
                if (resp.data && resp.data.listPembayaran) return (resolve(listBayar));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
