import React, { Fragment, useState, useEffect } from "react";
import { Row, Col, CardBody, Button } from "reactstrap";
import { Colxx, Separator } from "../../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../../containers/navs/Breadcrumb";
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import mTrans from '../../../../Controler/c_Transaksi'
import moment from 'moment-timezone'
import DateFnsUtils from '@date-io/date-fns';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import DataTablePagination from "../../../../components/DatatablePagination"
import Skeleton from 'react-loading-skeleton';
import { isArray } from "util";
import ReactTable from "react-table"
import { ContextMenuTrigger } from "react-contextmenu"
import Confirm from '../../../../helpers/Confirm'
import Notifikasi from '../../../../helpers/Notifikasi'
import ModalBatalJanji from './Modal.batalJanji'

const BuatJanji = props => {
    const authLogin = JSON.parse(localStorage.getItem('keminaAdmin'))
    const [isReload, setIsReload] = useState(true)
    const [listJanji, setListJanji] = useState([])
    const [janji, setJanji] = useState({})
    const [tanggal, setTanggal] = useState(moment())
    const [isConfirm, setIsConfirm] = useState(false)
    const [textConfirm, setTextConfirm] = useState("")
    const [isBatal, setIsBatal] = useState(false)




    useEffect(() => {
        setListJanji([])
        mTrans.listJanji(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, moment(tanggal).format("YYYY-MM-DD")).then(resp => {
            setListJanji(resp);
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsReload(false));
        return () => { };
    }, [authLogin.clientID, authLogin.token, authLogin.username, authLogin.klinik.kodeklinik, tanggal, isReload]);

    const loadingRow = [0, 1, 2, 3, 4, 5];
    const dataTableColumns = [
        {
            Header: "Pasien",
            accessor: "pasien",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: "Keperluan",
            accessor: "keperluanPasien",
            Cell: props => <p className="text-left"> {props.value}</p>
        },
        {
            Header: "Jam Mulai",
            accessor: "jamMulaiJanji",
            width: 100,
            Cell: props => <p className="text-left"> {props.value}</p>,

        },
        {
            Header: "Jam Selesai",
            accessor: "jamSelesaiJanji",
            width: 100,
            Cell: props => <p className="text-left"> {props.value}</p>,
        },
        {
            Header: "Dokter",
            accessor: "kodedokter",
            Cell: props => <p className="list-item-heading"> {props.value}</p>
        },
        {
            Header: <p className="text-center">Aksi</p>,
            filterable: false,
            Cell: props => isReload ? <Skeleton /> : <p className="text-center">
                <Button style={{ marginRight: 3 }} color="warning" onClick={konfirmasi.bind(this, props.original)}>Konfirmasi</Button>
                <Button color="danger" onClick={batal.bind(this, props.original)}>Batal</Button>
            </p>
        }
    ];
    const konfirmasi = data => {
        setJanji(data)
        setTextConfirm(`Yakin ingin konfirmasi pasien ${data.pasien} ini?`)
        setIsConfirm(true)
    }
    const handleConfirm = () => {
        mTrans.konfirmasiHadir(authLogin.clientID, authLogin.token, janji._id).then(resp => {
            if (resp) {
                Notifikasi("success", `Pasien ${janji.pasien} berhasil dikonfirmasi`, "")
                setIsReload(true)
                setIsConfirm(false)
            } else {
                Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
            }
        }).catch(err => {
            if (isArray(err) && err.length > 0) {
                Notifikasi("error", err[0].message, "Gagal Simpan")
            } else if (typeof err) {
                Notifikasi("error", err.message, "Gagal Simpan")
            } else {
                console.log(err);
            }
        })
    }
    const batal = (data) => {
        setIsBatal(true)
        setJanji(data)
    }
    return (
        <Fragment>
            <Row>
                <Colxx xxs="12">
                    <Breadcrumb heading="menu.konfirmasi-jadwal" match={props.match} />
                    <Separator className="mb-5" />
                </Colxx>
            </Row>
            <Row>
                <Col xs={12}>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                        <DatePicker
                            fullWidth={true}
                            label="Pilih Tanggal"
                            format="dd-MM-yyyy"
                            value={tanggal}
                            onChange={(d) => setTanggal(d)}
                            ampm={false}
                        />
                    </MuiPickersUtilsProvider>
                </Col>
                {isReload && (
                    <Col xs={12}>
                        <Card className="tangan mgTop10">
                            <CardContent>
                                {loadingRow.map((el, i) => (
                                    <Skeleton height={35} key={i} />
                                ))}
                            </CardContent>
                        </Card>
                    </Col>
                )}
                {(!isReload && listJanji.length === 0) && (
                    <Col xs={12} style={{ marginTop: 30 }}>
                        <h4 className="text-center2"><b>Belum ada pasien ngambil antrian untuk tanggal <i>{moment(tanggal).format("DD-MM-YYYY")}</i></b></h4>
                    </Col>
                )}
                {listJanji.length > 0 && (
                    <Col xs={12}>
                        <ContextMenuTrigger id="menu_id" >
                            <Card>
                                <CardBody>
                                    <ReactTable
                                        data={listJanji}
                                        columns={dataTableColumns}
                                        defaultPageSize={5}
                                        showPageJump={true}
                                        PaginationComponent={DataTablePagination}
                                        showPageSizeOptions={true}
                                        filterable
                                        defaultFilterMethod={(filter, row) => {
                                            const id = filter.pivotId || filter.id;
                                            return row[id] !== undefined
                                                ? String(row[id].toLowerCase()).includes(
                                                    filter.value.toLowerCase()
                                                )
                                                : true;
                                        }}
                                    />
                                </CardBody>
                            </Card>
                        </ContextMenuTrigger>
                    </Col>
                )}
            </Row>
            
            {isConfirm && <Confirm open={isConfirm} setOpen={setIsConfirm} textConfirm={textConfirm} oke={handleConfirm} />}
            {isBatal && <ModalBatalJanji  open={isBatal} setOpen={setIsBatal} janji={janji} authLogin={authLogin} setIsReload={setIsReload}/>}
        </Fragment>
    )
}
export default BuatJanji
